@import "./components/components.module.css";

:root {
  --default-font-color: #ffffff;
  --secondy-font-color: #8c8c8c;
  --background-cards: #5e5748;
  --default-background: #0d0c0c;
  --background-color-home: #272829;
  --default-red: #e23c2e;
  --default-green: #309746;
}

* {
  margin: 0;
  padding: 0;
  color: var(--default-font-color);
  font-family: "Liberation Sans", "Nimbus Sans L", "Helvetica Neue", Helvetica,
    Arial, sans-serif;
}

form {
  display: flex;
  flex-direction: column;
  width: 17rem;
}

@media only screen and (min-width: 769px) {
  form {
    width: 20rem;
  }
}
